.authentication-signin{
    .auth-content{
        height: 100vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        &>.row{
            width: 100%;
        }
    }
}

.auth-wrapper.auth-v2{
    .authentication-inner{
        height: 100vh;
        .img-side{
            img{
                width: 100%;
                height: 100vh;
                object-fit: cover;
            }
        }
       
    }
    .cover-img{
        width: 100%;
        height: 100vh;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    .auth-center{
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        margin: auto;
        transform: translateY(-50%);
    } 
}
.btn-label-facebook {
    color: #3b5998;
    border-color: rgba(0,0,0,0);
    background: #e0e4ef;
    &:hover{
        border-color: rgba(0,0,0,0) !important;
    background: #355089 !important;
    color: #fff !important;
    box-shadow: 0 0.125rem 0.25rem 0 rgba(59,89,152,.4) !important;
    transform: translateY(-1px) !important;
    }
}
.btn-label-twitter {
    color: #1da1f2;
    border-color: rgba(0,0,0,0);
    background: #dbf0fd;
    &:hover{
        border-color: rgba(0,0,0,0) !important;
        background: #1a91da !important;
        color: #fff !important;
        box-shadow: 0 0.125rem 0.25rem 0 rgba(29,161,242,.4) !important;
        transform: translateY(-1px) !important;
    }
}
.btn-label-google {
    color: #dd4b39;
    border-color: rgba(0,0,0,0);
    background: #fae2df;
    &:hover{
        border-color: rgba(0,0,0,0) !important;
        background: #c74433 !important;
        color: #fff !important;
        box-shadow: 0 0.125rem 0.25rem 0 rgba(221,75,57,.4) !important;
        transform: translateY(-1px) !important;
    }
}

@media only screen and (max-width: 767px){
    .authentication-signin{
        .auth-content{
            padding: 30px 10px;
        }
    }
}