.fc-view {
    margin-top: 30px;
}

.none-border {
    .modal-footer {
        border-top: none;
    }
}

.fc-toolbar {
    margin: 15px 0 5px;

    h2 {
        font-size: 1.25rem;
        line-height: 1.875rem;
        text-transform: uppercase;
    }
}

.fc-day-grid-event {
    .fc-time {
        font-weight: $font-weight-bold;
    }
}

.fc-day {
    background: transparent;
}

.fc-toolbar {
    .fc-state-active,
    .ui-state-active,
    .ui-state-hover,
    button:focus,
    button:hover {
        z-index: 0;
    }
}

.fc {
    th.fc-widget-header {
        background: $primary;
        color: #fff;
        border-color: $primary;
        font-size: 13px;
        line-height: 20px;
        padding: 10px 0;
        text-transform: uppercase;
    }
}

.fc-unthemed {
    .fc-divider,
    .fc-popover,
    .fc-row,
    tbody,
    td,
    th,
    thead {
        border-color: lighten($border-color,5%);
    }
}

.fc-button,
.fc-today-button {
    background: lighten($border-color,5%);
    border: none;
    color: $gray-800;
    text-shadow: none;
    text-transform: capitalize;
    box-shadow: none;
    border-radius: 3px;
    margin: 0.375rem 0.75rem;
    padding: 0.375rem 0.75rem !important;
    height: auto !important;
}

.fc-text-arrow {
    font-family: inherit;
    font-size: 1rem;
}

.fc-state-hover {
    background: $gray-200;
}

.fc-state-highlight {
    background: $gray-300;
}

.fc-state-active,
.fc-state-disabled,
.fc-state-down {
    background-color: $primary;
    color: $white;
    text-shadow: none;
}

.fc-cell-overlay {
    background: $gray-300;
}

.fc-unthemed {
    td.fc-today {
        background: lighten($gray-200,5%);
    }

    .fc-divider,
    .fc-list-heading td,
    .fc-popover .fc-header {
        background: $gray-100;
    }
}

.fc-event,
.fc-event:not([href]) {
    &:not([class*='bg-']) {
        background-color: $primary;
    }
    color: #fff;
    border-color: $primary;
    border-left-width: 4px;
    padding: 5px 10px;
    border-radius: 2px;
    margin-bottom: 5px;
    cursor: move;
}

.external-event {
    cursor: move;
    margin: 10px 0;
    padding: 8px 10px;
    color: $white;
}

.fc-basic-view {
    td.fc-week-number {
        span {
            padding-right: 8px;
        }
    }

    td.fc-day-number {
        padding-right: 8px;
    }

    .fc-content {
        color: $white;
    }
}

.fc-time-grid-event {
    .fc-content {
        color: $white;
    }
}

.fc .fc-button-group > *,
.fc-today-button {
    margin-bottom: 4px;
    margin-left: 10px;
    border-radius: 2px;
    font-size: 1rem;
}

.fc-ltr {
    .fc-basic-view {
        .fc-day-top {
            .fc-day-number {
                float: right;
                height: 20px;
                width: 20px;
                text-align: center;
                line-height: 20px;
                background-color: lighten($border-color,5%);
                border-radius: 2px;
                margin: 5px;
                font-size: 12px;
                border: 1px solid lighten($border-color,3%);
            }
        }
    }
}

.fc-scroller {
    overflow: visible !important;
    height: auto !important;
}

.fc-row .fc-highlight-skeleton {
    // background: transparentize($primary, 0.8);
}
@include media-breakpoint-down(sm) {
    .fc-toolbar {
        .fc-center,
        .fc-left,
        .fc-right {
            float: none;
            display: block;
            clear: both;
            margin: 10px 0;
        }
    }

    .fc {
        .fc-toolbar {
            > * {
                > * {
                    float: none;
                }
            }
        }
    }

    .fc-today-button {
        display: none;
    }
}

.fc-popover {
    background: $dropdown-bg;
}

.fc-h-event .fc-event-main {
    color: inherit;
    color: var(--fc-event-text-color, inherit);
}

.fc .fc-timegrid-slot {
    height: 2.5em;
}

.fc-timeGridDay-view {
    .fc-v-event .fc-event-main-frame {
        flex-direction: row;
        align-items: center;

        > div + div {
            margin-left: 10px;
        }
    }
}

.fc-event,
.fc-event:not([href]) {
    border-width: 1px 1px 1px 5px;
    border-style: solid;
    border-radius: 3px;
    @each $color, $value in $theme-colors {
        &.event-#{$color} {
            background: shift-color($value, $soft-bg-level);
            border-color: $value;
            color: $value;

            &.fc-v-event .fc-event-main {
                color: $value;
            }

            .fc-daygrid-event-dot {
                border-color: $value;
            }
        }
    }
}


.timeline-wrapper {
    position: relative;
    flex-wrap: nowrap;
    text-align: center;
    overflow-x: auto;
    padding-bottom: 20px;
  
    &::-webkit-scrollbar {
      height: 6px;
      opacity: 0;
      &:hover {
        opacity: 1;
      }
    }
    &::-webkit-scrollbar-track {
      background: transparent;
    }
    &::-webkit-scrollbar-thumb {
      background: shift-color($primary, $soft-bg-level);
    }
    &::-webkit-scrollbar-thumb:hover {
      background: $primary;
    }
    .timeline-icons {
      font-size: 30px;
      position: relative;
      z-index: 5;
      &:before,
      &:after {
        content: "";
        height: 2px;
        background: darken($body-bg, 5%);
        width: calc(50% - 16px);
        position: absolute;
        top: 22px;
        z-index: 1;
      }
      &:after {
        left: -12px;
      }
      &:before {
        right: -12px;
      }
      .timeline-dots {
        position: absolute;
        top: 22px;
        width: 50px;
        &:before,
        &:after {
          content: "";
          border: 3px solid darken($body-bg, 5%);
          position: absolute;
          top: -2px;
          z-index: 7;
          transform: rotate(45deg);
        }
        &:after {
          left: -6px;
        }
        &:before {
          right: -6px;
        }
      }
      i {
        display: inline-block;
        width: 50px;
        position: relative;
        z-index: 5;
      }
    }
    > div {
      &:first-child {
        .timeline-icons::after,
        .timeline-icons .timeline-dots::after {
          display: none;
        }
      }
      &:last-child {
        .timeline-icons::before,
        .timeline-icons .timeline-dots::before {
          display: none;
        }
      }
    }
  }
  // Timeline end


