    }

    .dropdown-menu {
      margin-top: 0;

      li {
        cursor: pointer;

        a {
          font-size: 14px;
          text-transform: capitalize;
        }
      }
    }

    .btn.dropdown-toggle {
      border: none;
      background: transparent;
      box-shadow: none;
      color: #888;

